.how-to{
	background: $white;
	&_no-transform{
		padding: 30px 0;
		.how-to__content{
			margin-top: 0;
		}
	}
}

.how-to__wrapper{
	padding: 1px 0 15px;
	max-width: 1400px;
	width: calc(100% - 40px);
	margin: 0 auto;
}

