	background-size: 90%;
	border-radius: 30px;
	padding: 50px 20px 10px;
	max-width: 1400px;
	width: calc(100% - 40px);
	margin: auto; 
	&:before{
		content: "";
		display: block;
		margin: 0 auto 8px;
		width: 6px;
		height: 36px;
		background: #5478E9;
		border-radius: 3px;
		transform: rotate(32deg);
	}	
}



.services__item{

	margin: 0 0 10px;
	border-radius: 30px;
	overflow: hidden;
	background: $mainColor;
	display: block;
	height: 380px;
	position: relative;
	color: $white;
	transition: all .2s ease-in;
	&:hover {
		background: $mainColorDarken
	}
}

.services__item-img{
	position: absolute;
	left: 0;
	top: 0;
	bottom: 0;
	right: 0;
	margin: auto;
	opacity: .3;

	img{
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		right: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;

	}
}


.services__item-content{
	position: absolute;
	left: 0;
	top: 0;
	bottom: 0;
	right: 0;
	padding: 50px 30px;
}


.services__item-arrow{
	position: absolute;
	bottom: 60px;
	left: 30px;
	width: 60px;
	height: 60px;
	border-radius: 50%;
	background: $white;
	z-index: 1;
	transition: all .2s ease-in;
	svg{
		position: absolute;
		width: 37%;
		height: 37%;
		transform: rotate(-90deg);
		top: 0;
		bottom: 0;
		right: 0;
		left: 0;
		margin: auto;
	}
}


@media screen and (min-width: 768px){


	.services__cards{
		display: flex;
		justify-content: space-between;
	}
	.services__item{
		width: calc(33.3333% - 8px);
		margin: 0;
	}
}


@media screen and (min-width: 1230px){


	.services__item{
		width: calc(33.3333% - 15px);
	}
	.services__content{
		padding: 60px 115px;
		background-position: center 163px;
		background-size: 82% auto;
	}


	.services__title-block{
		margin: 0 0 40px;
	}




}